'use client';

import { useAuth } from '@/hooks/useAuth';

export default function Header() {
    // Ambil data user yang sedang login menggunakan hook kita
    // Middleware 'auth' memastikan hanya user login yang bisa melihat data ini
    const { user } = useAuth({ middleware: 'auth' });

    return (
        <header className="h-16 bg-white border-b border-gray-200 flex items-center justify-between px-4 md:px-8 fixed top-0 right-0 left-0 md:left-64 z-10 transition-all">
            
            {/* BAGIAN KIRI: Judul Halaman */}
            <div className="flex items-center">
                {/* Nanti bisa dibuat dinamis sesuai halaman yang aktif */}
                <h2 className="text-lg font-semibold text-gray-800">Admin Portal</h2>
            </div>

            {/* BAGIAN KANAN: Profil User */}
            <div className="flex items-center gap-4">
                
                {/* Info Teks (Nama & Role) - Disembunyikan di layar HP agar rapi */}
                <div className="text-right hidden sm:block">
                    <p className="text-sm font-medium text-gray-900">
                        {user?.name || 'Memuat...'}
                    </p>
                    <p className="text-xs text-gray-500">
                        {user?.role?.name_role || 'Administrator'}
                    </p>
                </div>

                {/* Avatar Bulat (Inisial Nama) */}
                <div className="h-10 w-10 rounded-full bg-[#D93F21]/10 flex items-center justify-center border border-[#D93F21]/20 shadow-sm">
                    <span className="text-lg font-bold text-[#D93F21]">
                        {/* Ambil huruf pertama dari nama user, atau 'A' jika belum ada data */}
                        {user?.name ? user.name.charAt(0).toUpperCase() : 'A'}
                    </span>
                </div>
            </div>
        </header>
    );
}